import React, { Component } from "react";
import { Footer, FooterSection } from "react-mdl";

class FFooter extends Component {
  render() {
    return (
      <div>
        <Footer size="mini">
          <FooterSection>
            <div>
              <a
                style={{ fontSize: "27px", color: "#457cb2" }}
                href="https://github.com/DavGabrielian/React-Coin"
              >
                VIEW CODE HERE
                <i class="fa fa-fw fa-github" style={{ fontSize: "35px" }}></i>
              </a>
              <p style={{ position: "relative", bottom: "0" }}>
                Copyright © David Gabrielian 2020
              </p>
            </div>
          </FooterSection>
        </Footer>
      </div>
    );
  }
}

export default FFooter;
